.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    /* z-index: 1200; */
}

.Modal {
    width: 80%;
    /* height: 80%; */
    max-width: calc(100vw - 48px);
    max-height: calc(100vh - 24px);
    border-radius: 4px;
    text-align: center;
    /* background-color: #fff; */
}

.Modal img {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: contain;
}
